// components/FilterBar.tsx
"use client";
import { useState } from "react";
import "../css/components/filterbar.css";

export default function FilterBar({ onSearch }) {
  const [searchTerm, setSearchTerm] = useState("");

  const handleSearch = () => {
    if (searchTerm.length >= 2) {
      onSearch(searchTerm); // Callback-Funktion wird aufgerufen, wenn die Eingabe mindestens 2 Zeichen hat
    }
  };

  return (
    <div className="filter-bar">
      <input
        type="text"
        placeholder="Suche nach Workshops..."
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
        className="search-input"
      />
      <button className="search-button" onClick={handleSearch}>
        Suche
      </button>
    </div>
  );
}
